<!-- src/components/DetailsModal.vue -->
<template>
  <div class="modal-backdrop" @click="$emit('close')">
    <div class="modal-content" @click.stop>
      <button class="close-button" @click="$emit('close')">&times;</button>
      <h2>{{ item.name }}</h2>
      <div class="modal-body">
        <img 
          v-if="item.image" 
          :src="getImageUrl(item.image)" 
          :alt="item.name" 
          class="modal-image"
        >
        <p v-if="item.description">{{ item.description }}</p>
        <div v-if="category === 'music'" class="audio-player">
          <audio v-if="item.sampleTrack" controls>
            <source :src="getAudioUrl(item.sampleTrack)" type="audio/mpeg">
            Your browser does not support the audio element.
          </audio>
        </div>
        <div v-if="category === 'artists'" class="artwork-sample">
          <img 
            v-if="item.artworkSample" 
            :src="getImageUrl(item.artworkSample)" 
            :alt="item.name + ' artwork sample'" 
            class="sample-image"
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailsModal',
  props: {
    item: {
      type: Object,
      required: true
    },
    category: {
      type: String,
      required: true
    }
  },
  methods: {
    getImageUrl(image) {
      return `${process.env.BASE_URL}assets/favorites/${image}`;
    },
    getAudioUrl(audio) {
      return `${process.env.BASE_URL}assets/favorites/audio/${audio}`;
    }
  }
};
</script>

