  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: var(--background-color);
  padding: var(--spacing-large);
  border-radius: 10px;
  max-width: 80%;
  max-height: 80%;
  overflow-y: auto;
  position: relative;
}

.close-button {
  position: absolute;
  top: var(--spacing-small);
  right: var(--spacing-small);
  font-size: 1.5rem;
  background: none;
  border: none;
  cursor: pointer;
}

.modal-image {
  max-width: 100%;
  height: auto;
  margin-bottom: var(--spacing-medium);
}

.audio-player, .artwork-sample {
  margin-top: var(--spacing-medium);
}

.sample-image {
  max-width: 100%;
  height: auto;
}
</style>
