<!-- src/components/AboutTab.vue -->
<template>
  <div class="about-tab">
    <div v-if="tab === 'overview'" class="overview">
      <p v-for="(paragraph, index) in overviewParagraphs" :key="index">{{ paragraph }}</p>
    </div>
    <div v-else-if="tab === 'favorites'" class="favorites">
      <div v-for="(items, category) in favorites" :key="category" class="favorite-category">
        <h3>{{ formatCategory(category) }}</h3>
        <div class="favorite-grid">
          <div v-for="item in items" :key="item.name" class="favorite-item" @click="showDetails(category, item)">
            <div class="favorite-image">
              <img 
                v-if="item.image" 
                :src="getImageUrl(item.image)" 
                :alt="item.name" 
                @error="item.imageError = true"
                v-show="!item.imageError"
              >
              <div v-show="!item.image || item.imageError" class="favorite-image-placeholder">
                <font-awesome-icon :icon="getCategoryIcon(category)" />
              </div>
            </div>
            <div class="favorite-name">{{ item.name }}</div>
          </div>
        </div>
      </div>
    </div>
    <DetailsModal 
      v-if="selectedItem" 
      :item="selectedItem" 
      :category="selectedCategory"
      @close="selectedItem = null"
    />
  </div>
</template>

<script>
import { ref, computed } from 'vue';
import DetailsModal from './DetailsModal.vue';

export default {
  name: 'AboutTab',
  components: {
    DetailsModal
  },
  props: {
    tab: {
      type: String,
      required: true
    },
    overview: {
      type: String,
      required: true
    },
    favorites: {
      type: Object,
      required: true
    }
  },
  setup(props) {
    const overviewParagraphs = computed(() => props.overview.split('\n\n'));
    const selectedItem = ref(null);
    const selectedCategory = ref(null);

    const formatCategory = (category) => {
      return category.split(/(?=[A-Z])/).join(' ');
    };

    const getImageUrl = (image) => {
      return `${process.env.BASE_URL}assets/favorites/${image}`;
    };

    const getCategoryIcon = (category) => {
      const iconMap = {
        artists: 'palette',
        movies: 'film',
        books: 'book',
        youtubers: ['fab', 'youtube'],
        music: 'music',
        philosophers: 'brain',
        scientists: 'flask',
        tvShows: 'tv',
        podcasts: 'podcast',
        videoGames: 'gamepad',
        anime: 'dragon',
        cuisines: 'utensils',
        places: 'globe',
        programmingLanguages: 'code',
        artMovements: 'paint-brush',
        historicalFigures: 'landmark',
        inventions: 'lightbulb',
        hobbies: 'puzzle-piece',
        charities: 'hand-holding-heart',
        quotes: 'quote-left',
        blogs: 'rss',
        comics: 'book-open',
        fashionStyles: 'tshirt',
        mathematicalConcepts: 'square-root-alt',
        scientificTheories: 'atom',
        philosophicalConcepts: 'yin-yang',
        blockchainProjects: 'cubes'
      };
      return iconMap[category] || 'star';
    };

    const showDetails = (category, item) => {
      selectedCategory.value = category;
      selectedItem.value = item;
    };

    return {
      overviewParagraphs,
      formatCategory,
      getImageUrl,
      getCategoryIcon,
      showDetails,
      selectedItem,
      selectedCategory
    };
  }
};
</script>

<style scoped>
.favorite-category {
  margin-bottom: var(--spacing-large);
}

.favorite-category h3 {
  color: var(--highlight-color);
  margin-bottom: var(--spacing-medium);
  text-transform: capitalize;
}

.favorite-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: var(--spacing-medium);
}

.favorite-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.favorite-item:hover {
  transform: scale(1.05);
}

.favorite-image {
  width: 150px;
  height: 225px;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom: var(--spacing-small);
  position: relative;

}

.favorite-image img,
.favorite-image-placeholder {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 3rem;
}

.favorite-name {
  font-size: var(--font-size-small);
  font-weight: bold;
}
</style>
